.order-history {
  // Grid
  &-wrapper {
    padding: 10px;
    background-color: $header-bg-clr;

    @media (min-width: $xs) {
      padding: 15px 15px 30px;
    }

    @media (min-width: $md) {
      padding: 30px 30px 60px;
    }

    @media (min-width: $xxl) {
      padding: 60px;
    }
  }

  &__bg-block {
    margin-bottom: 3px;
    padding: 10px;
    background-color: $white;

    @media (min-width: $xs) {
      padding: 15px;
    }

    @media (min-width: $md) {
      margin-bottom: 5px;
      padding: 20px;
    }

    @media (min-width: $xl) {
      padding-right: 30px;
      padding-left: 30px;
    }
  }

  // Page title
  &-title {
    margin-bottom: 20px;

    @media (min-width: $md) {
      margin-bottom: 30px;
    }
  }

  // Order heading
  &__heading {
    display: flex;
    padding: 0 10px 10px;
    gap: 10px;

    @media (min-width: $xs) {
      gap: 18px;
      padding-right: 15px;
      padding-left: 15px;
    }

    @media (min-width: $md) {
      padding-right: 20px;
      padding-bottom: 15px;
      padding-left: 20px;
    }

    @media (min-width: $xl) {
      padding-right: 30px;
      padding-bottom: 20px;
      padding-left: 30px;
    }
  }

  &-id {
    flex: 1 1 85px;

    @media (min-width: $md) {
      flex-basis: 100px;
    }

    @media (min-width: $xl) {
      flex-basis: 160px;
    }
  }

  &-date,
  &-state,
  &-sum {
    flex: 1 1 70px;

    @media (min-width: $md) {
      flex-basis: 100px;
    }

    @media (min-width: $xl) {
      flex-basis: 160px;
    }
  }

  &-sum {
    @media (max-width: $max-md) {
      display: none;
    }
  }

  &-icon {
    flex: 0 0 12px;
    font-size: 0;
  }

  &-id,
  &-date,
  &-state,
  &-sum {
    @media (min-width: $md) {
      flex-basis: 100px;
    }
  }

  &__heading,
  .accordion__title {
    @media (max-width: $max-xs) {
      font-size: 10px;
    }
  }

  // Order body
  &__body {
    .accordion__title {
      gap: 10px;

      @media (min-width: $xs) {
        gap: 18px;
      }
    }

    .accordion__body {
      padding-top: 0;
    }
  }

  // Order item
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;

    @media (min-width: $md) {
      gap: 30px;
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 60px;
    height: 60px;
    border: 1px solid $bg-clr;

    @media (min-width: $md) {
      flex-basis: 100px;
      height: 100px;
    }

    img {
      width: 30px;
      height: 30px;

      @media (min-width: $md) {
        width: 45px;
        height: 45px;
      }
    }
  }

  &__info {
    @media (min-width: $md) {
      flex: 1 1 65%;
      padding: 0 12px;
    }

    @media (min-width: $xl) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-basis: 42%;
    }

    &-title {
      @media (max-width: $max-xs) {
        font-size: 16px;
      }

      &-wrapper {
        @media (min-width: $xl) {
          flex: 1 1 63%;
        }
      }
    }

    @media (min-width: $lg) {
      flex-basis: 68%;
    }

    &-size,
    &-sku {
      display: block;
      margin-top: 5px;
      font-family: $font-title;
      font-size: 10px;
      color: $gray;
    }

    &-size {
      @media (min-width: $md) {
        margin-top: 10px;
        font-size: 12px;
      }

      @media (min-width: $xl) {
        font-size: 16px;
      }
    }

    &-sku {
      @media (min-width: $md) {
        margin-top: 15px;
        font-weight: 600;
      }

      @media (min-width: $xl) {
        flex: 1 1 37%;
        margin-top: 0;
        font-size: 12px;
      }
    }
  }

  &__price {
    @media (max-width: $max-md) {
      position: absolute;
      right: 10px;
      bottom: 15px;
    }

    @media (min-width: $md) {
      font-size: 24px;
      flex: 1 1 35%;
    }

    @media (min-width: $lg) {
      flex-basis: 32%;
    }

    @media (min-width: $xlg) {
      flex-basis: 29%;
    }

    @media (min-width: $xl) {
      flex-grow: 0;
      flex-basis: 26%;
      font-size: 36px;
    }
  }
}

.order-state-finished {
  color: $main-clr;
}

.order-state-progress {
  color: $secondary-clr;
}
