.products {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;

  @media (min-width: $md) {
    gap: 20px 16px;
  }

  @media (min-width: $xl) {
    gap: 30px 20px;
  }

  @media (min-width: $xxl) {
    column-gap: 25px;
  }

  &-heading {
    margin-bottom: 30px;

    @media (min-width: $md) {
      margin-bottom: 60px;
    }
  }

  &__item {
    flex: 0 0 calc(50% - 5px);

    @media (min-width: $md) {
      flex-basis: calc(33.33% - 11px);
    }

    @media (min-width: $xl) {
      flex-basis: calc(25% - 15px);
    }

    @media (min-width: $xxl) {
      flex-basis: calc(20% - 20px);
    }
  }

  &__banner {
    flex: 1 1 100%;
    margin: 25px 0;

    @media (min-width: $xl) {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 50px;
      order: 10!important;
      margin: 30px 0;
    }

    @media (min-width: $xxl) {
      gap: 25px;
      order: 12!important;
    }

    &-text {
      text-align: center;

      @media (min-width: $xxl) {
        flex: 1 1 37%;
      }

      h2 {
        margin-bottom: 15px;
      }
    }

    &-desc {
      @media (min-width: $sm) {
        margin: 0 auto;
        max-width: 575px;
      }

      @media (min-width: $xxl) {
        max-width: 650px;
      }
    }

    &__image {
      height: 280px;

      @media (max-width: $max-xl) {
        margin-top: 20px;
      }

      @media (min-width: $md) {
        height: 450px;
      }

      @media (min-width: $xl) {
        flex: 1 1 63%;
        height: 495px;
      }

      @media (min-width: $xxl) {
        max-width: 1020px;
        height: 630px;
      }
    }
  }
}
