.account {
  padding: 15px;
  background-color: $white;

  @media (min-width: $md) {
    padding: 30px;
  }

  @media (min-width: $xl) {
    display: flex;
    flex-wrap: wrap;
    gap: 50px 30px;
    padding: 50px;
  }

  @media (min-width: $xxl) {
    row-gap: 90px;
    padding: 60px;
  }

  .form__block {
    @media (max-width: $max-xl) {
      margin-bottom: 30px;
    }

    @media (min-width: $xl) {
      flex: 1 1 calc(50% - 15px);
    }
  }

  .form__title {
    margin-bottom: 20px;

    @media (min-width: $xl) {
      margin-bottom: 50px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 30px;
    }
  }

  &__form-action {
    @media (min-width: $xl) {
      flex: 1 1 100%;
    }
  }

  // Блок отображения данных
  &__show-data {
    .form__input {
      display: flex;
      height: 22px;
      color: $secondary-clr;

      @media (min-width: $md) {
        height: 24px;
      }

      @media (min-width: $xl) {
        height: 35px;
      }
    }

    &-label {
      display: block;
      margin-bottom: 4px;
      font-size: 10px;
      font-weight: 600;
      color: $gray;

      @media (min-width: $xl) {
        margin-bottom: 6px;
      }
    }
  }
}
